<template>   
 <div id="container"></div>

</template>
<script>
import osc from "osc";
//import * as Three from 'three';

var port = new osc.WebSocketPort({
          url: "wss://circusfamilyprojects.nl:8084" //  ws://localhost:8083 online server wss://circusfamilyprojects.nl:8084
        }); 

 port.open();

export default {
   
  data() {
  return{
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      //Three

   }
  },
  computed: {
  
  },
  created() {
    
    },
  mounted(){
   // this.Three = window.Three = require('Three');
    this.init();
    this.animate();
  
    },
  methods:{
    

    init: function() {
       
        let container = document.getElementById('container');

         this.camera = new this.$three.PerspectiveCamera(100, container.clientWidth/container.clientHeight, 0.1, 100);
        this.camera.position.z = 15;

        this.scene = new this.$three.Scene();

         var geometry = new this.$three.Geometry();
         //vlak bovenkant
        geometry.vertices.push(new this.$three.Vector3(0, 0,  0.0));  // x,y,z  0  // 0 is de hoogte
        geometry.vertices.push(new this.$three.Vector3(2, 2,  0.0));  // x,y,z  1
        geometry.vertices.push(new this.$three.Vector3(-2, 2,  0.0));  // x,y,z  2
        geometry.vertices.push(new this.$three.Vector3(2, -2,  0.0));  // x,y,z  3
        geometry.vertices.push(new this.$three.Vector3(-2, -2,  0.0));  // x,y,z  4
        
        geometry.vertices.push(new this.$three.Vector3(2, 2,  9));  // x,y,z  5  // 6 is de hoogte
        geometry.vertices.push(new this.$three.Vector3(-2, 2,  9));  // x,y,z  6
        geometry.vertices.push(new this.$three.Vector3(2, -2,  9));  // x,y,z  7
        geometry.vertices.push(new this.$three.Vector3(-2, -2, 9));  // x,y,z  8
        
    
       

        geometry.faces.push(
        // top
        new this.$three.Face3(0, 1, 2), // vlak 1
        new this.$three.Face3(0, 3, 1), // vlak 2
        new this.$three.Face3(0, 4, 3), // vlak 3
        new this.$three.Face3(0, 2, 4), //vlak 4

         new this.$three.Face3(2, 1, 5), // zijkant vlak 1
         new this.$three.Face3(2, 5, 6), // zijkant vlak 1
         new this.$three.Face3(1, 7, 5), // zijkant vlak 2
         new this.$three.Face3(1, 3, 7), // zijkant vlak 2
         new this.$three.Face3(3, 8, 7), // zijkant vlak 3
         new this.$three.Face3(3, 4, 8), // zijkant vlak 3
         new this.$three.Face3(4, 6, 8), // zijkant vlak 4
         new this.$three.Face3(4, 2, 6), // zijkant vlak 4
              )
          
     var faces = geometry.faces;
console.log(faces)
      for ( var face of faces ) {

        face.color.set( Math.random() * 0xffffff );

      }

      geometry.colorsNeedUpdate = true;

            // geometry.computeFaceNormals();
            // geometry.computeVertexNormals();
          var material = new this.$three.MeshBasicMaterial({ vertexColors: this.$three.FaceColors, side: this.$three.DoubleSide});  //MeshBasicMaterial({color: 0xFF0000, wireframe: true}); MeshNormalMaterial(); 
          this.mesh = new this.$three.Mesh( geometry, material );
         // this.mesh.rotation.x += 2;
        //  this.mesh.rotation.y += 2;
          this.scene.add(this.mesh);



        this.renderer = new this.$three.WebGLRenderer({antialias: true});
        this.renderer.setSize(container.clientWidth, container.clientHeight);
        container.appendChild(this.renderer.domElement);

    },
    animate: function() {
        requestAnimationFrame(this.animate);
       this.mesh.rotation.x += 0.01;    
       this.mesh.rotation.y += 0.02;
        this.renderer.render(this.scene, this.camera);
    }


    }
}
</script>

<style lang="scss" scoped>

#container {
  
  height: 100%;
  width: 100%;

}




 

</style>